<template>
	<v-data-table
		class="g-groomer-services-table"
		:items-per-page="10"
		:headers="headers"
		:items="ordersList"
		no-data-text="   "
		no-results-text="   "
		loading-text=""
		color="#FFC11C"
		:footer-props="{
			itemsPerPageText: '  ',
			itemsPerPageAllText: '',
			showCurrentPage: true,
			showFirstLastPage: false,
			pageText: `${currentPage}  ${pageCount}`,
		}"
		@pagination="setCurrentPage"
	>
		<template v-slot:item="{ item }">
			<tr class="pointer" @click="showDetail(item.data)">
				<td class="py-2">
					{{ item.date }}
				</td>
				<td class="text-xs-right py-2">
					{{ item.services }}
				</td>
				<td class="text-xs-right py-2">
					{{ item.products }}
				</td>
				<td class="text-xs-right py-2 text-center">
					{{ item.groomer }}
				</td>
				<td class="text-xs-right py-2 text-center">
					{{ item.client }}
				</td>
				<td class="text-xs-right py-2 text-center">
					<span
						class="g-groomer-services-table__status"
						:class="`color-${item.status}`"
					>
						{{ statuses[item.status] }}
					</span>
				</td>
				<td class="text-xs-right py-2 text-right">
					{{ item.price }}
				</td>
			</tr>
		</template>
	</v-data-table>
</template>

<script>
export default {
	name: 'GGroomerServicesTable',
	props: {
		orders: {
			type: Array,
			default: () => [],
		},
	},
	data: () => ({
		headers: [
			{ text: '', value: 'date', width: 140 },
			{ text: '', value: 'services' },
			{ text: '', value: 'products' },
			{ text: '', value: 'groomer', width: 200, align: 'center' },
			{ text: '', value: 'client', width: 200, align: 'center' },
			{ text: '', value: 'status', width: 200, align: 'center' },
			{ text: ', ', value: 'price', width: 130 },
		],
		currentPage: 0,
		pageCount: 0,
		statuses: {
			success: '',
			cancel: '',
			reserved: '',
		},
	}),
	computed: {
		ordersList() {
			return this.orders.reduce(
				(
					acc,
					order,
				) => {
					const {
						updateDate,
						services,
						products,
						price,
						isSuccess,
						isCancel,
						isReserved,
						master,
						client,
					} = order
					const servicesStr = services
						.map(({ service }) => service.title)
						.join(', ');
					const productsStr = products
						.map(({ product }) => product.title)
						.join(', ');

					let status = 'any';

					if (isCancel) status = 'cancel';
					if (isReserved && !isCancel) status = 'reserved';
					if (isSuccess && !isCancel) status = 'success';

					return [
						...acc,
						{
							date: new Date(updateDate).toLocaleDateString(),
							services: servicesStr,
							products: productsStr,
							status,
							price,
							groomer: master ? `${master.username} ${master.lastname}` : '-',
							client: client ? `${client.username} ${client.lastname}` : '-',
							data: order
						},
					];
				},
				[],
			);
		},
	},
	mounted() {},
	methods: {
		setCurrentPage({ page, pageCount }) {
			this.currentPage = page;
			this.pageCount = pageCount;
		},
		showDetail(item) {
			this.$emit('onLineClick', item)
		}
	},
};
</script>

<style lang="scss">
.g-groomer-services-table {
	&__status {
		&.color-success {
			color: #72c565;
		}
		&.color-cancel {
			color: #ff5252;
		}
		&.color-reserved {
			color: rgba(36, 49, 56, 0.38);
		}
	}
}
</style>
